<template>
<div>
    <div class="card">
        <div class="card-header border-0">
            <div class="row">
                <div class="col-6">
                    <h3 class="mb-0">Student List</h3>
                </div>
            </div>
        </div>

        <el-table class="table-responsive align-items-center table-flush" header-row-class-name="thead-light" :data="users">
            <el-table-column label="Name" min-width="200px" prop="name" sortable>
                <template v-slot="{ row }">
                    <img :src="row.image" class="avatar rounded-circle mr-3" />
                    <router-link class="font-weight-bold  tk-font-color-default" :to="{name: 'studentDetail'}">
                        <b>{{ row.name }}</b>
                    </router-link>
                </template>
            </el-table-column>
            <el-table-column label="Created At" prop="createdAt" min-width="160px" sortable>
            </el-table-column>

            <el-table-column label="Instrument" min-width="130px" prop="lesson" sortable>
                <template v-slot="{ row }">
                    <a href="#!" class="font-weight-bold  tk-font-color-default">{{ row.lesson }}</a>
                </template>
            </el-table-column>

            <el-table-column label="Lesson Type" min-width="160px" prop="lesson" sortable>
                <template v-slot="{ row }">
                    <a href="#!" class="font-weight-bold  tk-font-color-default">{{ row.lessonType }}</a>
                </template>
            </el-table-column>

            <el-table-column min-width="180px" align="center">
                <div slot-scope="{ row }" class="table-actions">
                    <el-tooltip content="Edit" placement="top">
                        <a href="#!" @click.prevent="onEdit(row)" class="table-action" data-toggle="tooltip" data-original-title="Edit">
                            <i class="fas fa-user-edit"></i>
                        </a>
                    </el-tooltip>
                    <el-tooltip content="Archive" placement="top">
                        <a href="#!" @click.prevent="onArchive(row)" class="table-action" data-toggle="tooltip" data-original-title="Archive">
                            <i class="fas fa-inbox"></i>
                        </a>
                    </el-tooltip>
                    <el-tooltip content="Delete" placement="top">
                        <a href="#!" @click.prevent="onDelete(row)" class="table-action table-action-delete" data-toggle="tooltip" data-original-title="Delete">
                            <i class="fas fa-trash"></i>
                        </a>
                    </el-tooltip>
                </div>
            </el-table-column>
        </el-table>

        <div class="card-footer py-4 d-flex justify-content-end" v-if="users.length > 0">
            <base-pagination v-model="currentPage" :total="50"></base-pagination>
        </div>
    </div>
    <div class="card" v-if="archive.length > 0">
        <div class="card-header border-0">
            <div class="row">
                <div class="col-6">
                    <h3 class="mb-0">Archive Students</h3>
                </div>
            </div>
        </div>

        <el-table class="table-responsive align-items-center table-flush" header-row-class-name="thead-light" :data="archive">
            <el-table-column label="Name" min-width="200px" prop="name" sortable>
                <template v-slot="{ row }">
                    <img :src="row.image" class="avatar rounded-circle mr-3" />
                    <router-link class="font-weight-bold  tk-font-color-default" :to="{name: 'studentDetail'}">
                        <b>{{ row.name }}</b>
                    </router-link>
                </template>
            </el-table-column>
            <el-table-column label="Created At" prop="createdAt" min-width="160px" sortable>
            </el-table-column>

            <el-table-column label="Lesson" min-width="130px" prop="lesson" sortable>
                <template v-slot="{ row }">
                    <a href="#!" class="font-weight-bold  tk-font-color-default">{{ row.lesson }}</a>
                </template>
            </el-table-column>

            <el-table-column label="Lesson Type" min-width="160px" prop="lesson" sortable>
                <template v-slot="{ row }">
                    <a href="#!" class="font-weight-bold  tk-font-color-default">{{ row.lessonType }}</a>
                </template>
            </el-table-column>

            <el-table-column min-width="180px" align="center">
                <div slot-scope="{ row }" class="table-actions">
                    <el-tooltip content="Edit" placement="top">
                        <a href="#!" @click.prevent="onEdit(row)" class="table-action" data-toggle="tooltip" data-original-title="Edit">
                            <i class="fas fa-user-edit"></i>
                        </a>
                    </el-tooltip>
                    <el-tooltip content="Archive" placement="top">
                        <a href="#!" @click.prevent="onActive(row)" class="table-action" data-toggle="tooltip" data-original-title="Archive">
                            <i class="fas fa-undo-alt"></i>
                        </a>
                    </el-tooltip>
                    <el-tooltip content="Delete" placement="top">
                        <a href="#!" @click.prevent="onDeleteArchive(row)" class="table-action table-action-delete" data-toggle="tooltip" data-original-title="Delete">
                            <i class="fas fa-trash"></i>
                        </a>
                    </el-tooltip>
                </div>
            </el-table-column>
        </el-table>

        <div class="card-footer py-4 d-flex justify-content-end" v-if="archive.length > 0">
            <base-pagination v-model="currentPage2" :total="50"></base-pagination>
        </div>
    </div>

    <create-student
      v-if="showCreate" title="Edit Student" :name="fullName" :isEdit="true"
      @close="showCreate = false"
      @update="showCreate = false"
    ></create-student>

    <modal :show.sync="showDelete">
        <template slot="header">
            <h5 class="modal-title" id="exampleModalLabel">
                Delete Student
            </h5>
        </template>
        <div class="tk-popup-content">
            Do you want to delete the student <strong class="tk-font-color-black1" v-if="index >= 0">[{{users[index].name}}]</strong> ?
        </div>
        <template slot="footer">
            <base-button type="secondary" @click.prevent="showDelete = false" class="tk-font-color-lightGray tk-border-none">Close</base-button>
            <base-button type="primary" @click.prevent="confirmDelete" class="tk-font-color-white tk-bg-color-red tk-border-none">Delete</base-button>
        </template>
    </modal>
</div>
</template>

<script>
import users from "../studentList";
import archive from "../archive";
import CreateStudent from "@/tkViews/components/Modal/CreateStudent"
import {
    Table,
    TableColumn,
    DropdownMenu,
    DropdownItem,
    Dropdown,
    Tooltip,
} from "element-ui";

export default {
    name: "inline-actions-table",
    components: {
        [Tooltip.name]: Tooltip,
        [Table.name]: Table,
        [TableColumn.name]: TableColumn,
        [Dropdown.name]: Dropdown,
        [DropdownItem.name]: DropdownItem,
        [DropdownMenu.name]: DropdownMenu,
        CreateStudent,
    },
    data() {
        return {
            users,
            archive,
            currentPage: 1,
            currentPage2: 1,
            showDelete: false,
            showEditTeacher: false,
            isInvalid: false,
            deleteArchive: false,
            showCreate: false,
            fullName: '',
            phone: '',
            email: '',
            index: -1
        };
    },
    methods: {
        onEdit(row) {
            // alert(`You want to edit ${row.name}`);
            let self = this;
            self.fullName = row.name
            self.phone = row.phone
            self.email = row.email
            self.showCreate = true
        },
        onDelete(row) {
            // alert(`You want to delete ${row.name}`);
            let self = this
            self.users.some((item, i) => {
                if (item.id === row.id) {
                    self.index = i
                    self.showDelete = true
                }
            })
        },
        onDeleteArchive(row) {
            // alert(`You want to delete ${row.name}`);
            let self = this
            self.archive.some((item, i) => {
                if (item.id === row.id) {
                    self.index = i
                    self.showDelete = true
                    self.deleteArchive = true
                }
            })
        },
        confirmDelete() {
            let self = this
            self.showDelete = false
            if (self.deleteArchive) {
              self.archive.splice(self.index, 1)
              self.deleteArchive = false
            }else {
              self.users.splice(self.index, 1)
            }
            self.index = -1
        },
        onArchive(row) {
            // alert(`You want to archive ${row.name}`);
            let self = this
            self.users.some((item, i) => {
                if (item.id === row.id) {
                    self.archive.push(item)
                    self.users.splice(i, 1)
                }
            })
        },
        onActive(row) {
            // alert(`You want to archive ${row.name}`);
            let self = this
            self.archive.some((item, i) => {
                if (item.id === row.id) {
                    self.users.push(item)
                    self.archive.splice(i, 1)
                }
            })
        },
        validationEmail() {
            let self = this;
            if (self.$tools.isNull(self.email)) {
                self.isInvalid = false;
            } else {
                self.isInvalid = !self.$tools.isEmail(self.email);
            }
        },
    },
};
</script>
